<template>
    <div>
      <div class="p-2" @click="isInfo=false">
          <div  v-for="(item,index) in data.chat.data" :key="index">
            <div class="d-flex flex-row-reverse align-items-end" v-if="item.user_id===data.userinfo.id">
                <div class="bd-highlight">
                    <img :src="$store.state.imgurl+'/assets/img/userimg/'+item.avatar+'.jpg'" width="40" height="40" class="rounded-pill border border-2 border-white">
                </div>
                <div class="bd-highlight bg-info rounded rounded-10 rounded-start border border-info p-2 mx-1">{{item.text}}</div>
                <div class="bd-highlight">
                    <span v-if="item.state==='2'">未读</span>
                    <span v-if="item.state==='1'">已读</span>
                    <span v-if="item.state==='0'">发送失败</span>
                </div>
            </div>
            <div class="d-flex flex-row align-items-end" v-else>
                <div class="bd-highlight">
                    <img :src="$store.state.imgurl+'/assets/img/userimg/'+item.avatar+'.jpg'" width="40" height="40" class="rounded-pill border border-2 border-white">
                </div>
                <div class="bd-highlight bg-white rounded rounded-10 rounded-start border p-2 mx-1">{{item.text}}</div>
            </div>
          </div>
      </div>
      <div class="bg-white position-fixed start-0 end-0 bottom-0 ">
          <div class="p-2">
                <div class="row g-0">
                    <div class="col-auto">
                        <span class="d-inline-block text-center border rounded-pill bg-white fs-6 text-dark me-2 bi bi-mic-mute-fill" style="width:25px; height: 25px;"></span>
                    </div>
                    <div class="col">
                        <div class="d-grid">
                            <input class="form-control form-control-sm bg-light border-0" ref="input" placeholder="请输入内容" @click="isInfo=false"/>
                        </div>
                    </div>
                    <div class="col-auto">
                        <span class="d-inline-block text-center border rounded-pill bg-white fs-6 text-dark ms-2 bi" :class="isInfo===true?'bi-grid-3x2-gap':'bi-chat-dots'" @click="onisinfo()" style="width: 25px; height: 25px;"></span>
                        <span class="d-inline-block text-center border rounded-pill bg-white fs-6 text-dark ms-2 bi bi-geo-alt" style="width: 25px; height: 25px;"></span>
                    </div>
                </div>
          </div>
          <div v-if="isInfo">
              <div class="border-bottom border-light p-2">请问是2个人出行吗？</div>
              <div class="border-bottom border-light p-2">请问有携带宠物或者大件行李吗？</div>
              <div class="border-bottom border-light p-2">车上有亲友，你是否愿意同行？</div>
          </div>
      </div>
    </div>
</template>

<script>
import Input from '../form/input.vue'
export default {
  components: {
    Input 
  },
  props: {
    set:{},
    data:{},
  },
  data(){
      return {
          isInfo:true,
      }
  },
  methods:{
      onisinfo(){
          if(this.isInfo===true){
              this.isInfo=false;
              this.$refs.input.focus();
          }else{
              this.isInfo=true;
          }
      }
      
  }
}
</script>

<style>

</style>


